@font-face {
  font-family: 'Pretendard-Regular';
  src: url('https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
}

.App {
  margin: 0;
  padding-top: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: block;
  height: 100dvh;
  width: 100dvw;
  text-align: center;
  overflow: auto;
  position: relative;
}

p {
  margin: 0;
  color: white;
}
.word {
  font-size: 70px;
  padding: 30px 10px;
  font-weight: 1000;
  font-family: 'Pretendard-Regular';
}
.select {
}

.sBox {
  padding: 20px 0px;
  background-color: white;
  zoom: 0.9;
  width: 700px;
  height: 50px;
  margin: auto;
  margin-top: 10px;
  font-size: 27px;
  font-family: Pretendard-Regular;
  font-weight: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.red {
  color: red;
}
.yellow {
  background-color: rgb(215, 215, 38);
}
.timer {
  margin: 10px;
}
.top {
  padding-top: 100px;
}

@media screen and (max-width: 1024px) {
  .word {
  }
}
@media screen and (max-width: 768px) {
  .word {
    font-size: 50px;

    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
  }
  .sBox {
    width: 100vw;
    height: 40px;
    font-size: 20px;
  }
  .select {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
  }
}
